import { Box, Chip, Divider, IconButton, Link, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import { ExampleImages } from "../../static/exampleImages";
import BookmarkIcon from '@mui/icons-material/Bookmark';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import PersonIcon from '@mui/icons-material/Person';
import EventIcon from '@mui/icons-material/Event';
import PlaceIcon from '@mui/icons-material/Place';


const Start = () => {

    // eslint-disable-next-line
    const history = useNavigate();

    return (
        <Box component="main" className="flex-column w100">
            <Box className="flex-column gap-2">
                {Object.keys(ExampleImages).map((el) => (
                    <Box 
                        className="flex-column gap-0 memory"
                        key={ExampleImages[el][0]}
                    >
                        <Box className="flex-row jcsb w50">
                            <Box className="flex-row jcfs">
                                <IconButton disabled>
                                    <PersonIcon />
                                </IconButton>
                                <Typography variant="body2" className="note">
                                    {ExampleImages[el][3]}
                                </Typography>
                            </Box>
                            <Box className="flex-row">
                                <IconButton disabled>
                                    <EventIcon />
                                </IconButton>
                                <Typography variant="body2" className="note">
                                    {ExampleImages[el][4]}
                                </Typography>
                            </Box>
                        </Box>
                        <img 
                            src={ExampleImages[el][0]}
                            alt={"test"}
                        />
                        <Box className="flex-row jcsb w50">
                            <Box className="flex-row">
                                <Box className="flex-row gap-0">
                                    <IconButton>
                                        <BookmarkIcon />
                                    </IconButton>
                                    {/* <Typography variant="body1" className="note"> </Typography> */}
                                </Box>
                                <Box className="flex-row gap-0">
                                    <IconButton disabled>
                                        <CameraAltIcon />
                                    </IconButton>
                                    <Typography variant="body2" className="note">
                                        {ExampleImages[el][2]}
                                    </Typography>
                                </Box>
                            </Box>
                            <Box className="flex-row">
                                <IconButton disabled>
                                    <PlaceIcon />
                                </IconButton>
                                <Typography variant="body2" className="note">
                                    {ExampleImages[el][1]}
                                </Typography>
                            </Box>
                        </Box>
                        <Box className="flex-row gap-0 wrap note2 jcfs w50">
                            <Chip label="#" variant="outlined" />
                        </Box>
                        <Divider variant="middle" className="w40" sx={{ mt: 3 }}></Divider>
                    </Box>
                ))}
                <Box className="flex-column gap-0">
                    <Typography variant="body1" className="note">   </Typography>
                    <Box className="flex-row wrap">
                        <Typography variant="body2" className="note">
                            <Link underline="always" className="note"></Link>
                        </Typography>
                        <Typography variant="body2" className="note">&nbsp;   </Typography>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}

export default Start;